
<template>
  <a-row type="flex" justify="end"  style="min-width: 1200px">
    <a-col style="width: 85%">
      <a-layout>
        <a-layout-content style="padding: 0 50px">
          <!--      返回-->
          <a-row type="flex" justify="start">
            <a-col :span="1">
              <router-link to="/allDemand">
                <a-button type="link">
                  <a-icon type="left" />返回
                </a-button>
              </router-link>
            </a-col>
          </a-row>
          <!--      面包屑-->
          <a-breadcrumb style="margin: 16px 0">
            <a-breadcrumb-item><a @click="routerTo(true,'/allDemand')">需求</a></a-breadcrumb-item>
            <a-breadcrumb-item><a @click="routerTo(false,false)">需求详情</a></a-breadcrumb-item>
            <a-breadcrumb-item v-if="showBread">订单详情</a-breadcrumb-item>
          </a-breadcrumb>
          <div class="mlist-control ant-list-bordered">
            <div >
              <h2>{{needList.needsFarmerType}}   {{needList.needsDes}}</h2>
              <h4>需求发布时间: {{needList.needsTime}}&emsp;|&emsp;工程开工时间: {{needList.needsBeginTime}}&emsp;|&emsp;工程停工时间: {{needList.needsEndTime}}</h4>
            </div>
            <div style="margin-left: auto;margin-right: 40px">
              <h2 :style="machineStatusStyle(needList.needsType)">{{needList.needsType}}</h2>
            </div>
          </div>
          <a-tabs
            defaultActiveKey="1"
            @change="changeBread"
            style="margin-top:16px;background-color:white;width: 90%;margin-left: auto;margin-right: auto;min-width: 1200px"
          >
            <a-tab-pane tab="需求内容" key="1" @click="changeBread">
              <a-card style="margin-top: 24px" :bordered="false" title="基本需求"
                      :style="{backgroundImage: 'url(' + img + ')',
                  backgroundRepeat:no-repeat,center,top,
                  backgroundSize:'100% 100%'
                  }">
                <a-collapse>
                  <a-collapse-panel
                    v-for="items in matchOrderDes"
                    :key="items"
                    v-bind:header="items.group_leader"
                  >
                    <a-table
                      :columns="columns"
                      :dataSource="items.members"
                      :pagination="{ pageSize: 3 }"
                    ></a-table>
                  </a-collapse-panel>
                </a-collapse>
                <a-form id="components-form-demo-validate-other" :form="form">
                  <a-form-item label="工作地点" :labelCol="{ span: 2 }" :wrapperCol="{ span: 14 }">
                    <span class="ant-form-text">{{needList.needsLocation}}</span>
                  </a-form-item>
                  <a-form-item label="开工时间" :labelCol="{ span: 2 }" :wrapperCol="{ span: 14 }">
                    <span class="ant-form-text">{{needList.needsBeginTime}}</span>
                  </a-form-item>
                  <a-form-item label="交工时间" :labelCol="{ span: 2 }" :wrapperCol="{ span: 14 }">
                    <span class="ant-form-text">{{needList.needsEndTime}}</span>
                  </a-form-item>
                  <a-form-item label="总酬劳" :labelCol="{ span: 2 }" :wrapperCol="{ span: 14 }">
                    <span class="ant-form-text">{{needList.price}}</span>
                  </a-form-item>
                  <a-form-item label="备注" :labelCol="{ span: 2 }" :wrapperCol="{ span: 14 }">
                    <p class="ant-form-text">{{needList.remarks}}</p>
                  </a-form-item>
                </a-form>
              </a-card>
            </a-tab-pane>
            <a-tab-pane tab="订单详情" key="2" @click="changeBread" type="flex">
              <a-card
                v-for="(items,index) in matchOrderDes"
                :key="items.id"
                style="margin-top: 24px"
                :bordered="false"
                v-bind:title="'订单'+(index+1)"
                :style="{backgroundImage: 'url(' + img + ')',
                  backgroundRepeat:no-repeat,center,top,
                  backgroundSize:'100% 100%'
                  }"
              >
                <p
                  style="fontSize: 20px;color: rgba(0, 0, 0, 0.85); marginBottom: 16px;fontWeight: 500"
                >{{items.group_leader}} 工头</p>
                <a-table
                  :columns="columns"
                  :dataSource="items.members"
                  :pagination="{ pageSize: 6 }"
                ></a-table>
                <p slot="extra">订单编号：{{items.id}}&emsp; |&emsp;交易时间：{{items.lastModified}}</p>
                <h2 style="color: #901E02;font-weight: bold;text-align:right">订单金额：{{items.money}}</h2>
              </a-card>

              <a-divider>
                <h3 style="width:124px;
          line-height: 40px;
          color: white;
          height:42px;
          background:rgba(184,12,10,1);
          box-shadow:0px 2px 20px 0px rgba(171,171,171,0.5);
          border-radius:21px;">
                  合计
                </h3>
              </a-divider>
                <div type="flex" justify="center">
                  <a-form id="components-form-demo-validate-other" :form="form" >
                    <a-form-item label="订单数量" :labelCol="{ span: 4 }" :wrapperCol="{ span: 4 }">
                      <span class="ant-form-text">{{matchOrderDes.length}}</span>
                    </a-form-item>
                    <a-form-item label="订单总计" :labelCol="{ span: 4 }" :wrapperCol="{ span: 4 }">
                      <span class="ant-form-text">{{orderDes.allMoneyToFarmers}}</span>
                    </a-form-item>
                    <a-form-item label="平台服务" :labelCol="{ span: 4 }" :wrapperCol="{ span: 4 }">
                      <span class="ant-form-text">{{orderDes.allMoneyToApp}}</span>
                    </a-form-item>
                    <a-form-item label="费用综合" :labelCol="{ span: 4 }" :wrapperCol="{ span: 4 }">
                      <p class="ant-form-text" style="color: #901E02;font-weight: bold">{{orderDes.allMoney}}</p>
                    </a-form-item>
                  </a-form>
                </div>


            </a-tab-pane>
          </a-tabs>
        </a-layout-content>
        <a-layout-footer style="textAlign: center">DDDB ©2019 Created by 六芒星</a-layout-footer>
      </a-layout>
    </a-col>
  </a-row>
</template>

<script>
import DetailList from "@/components/DescriptionList";
import { getDemandDetailById, getOrderDetailById } from "../../api/login";

const DetailListItem = DetailList.Item;
const columns = [
  {
    title: "姓名",
    dataIndex: "name",
    key: "name",
    width: "20%"
  },
  {
    title: "身份证号",
    dataIndex: "IDCard",
    key: "IDCard",
    width: "40%"
  },
  {
    title: "联系方式",
    dataIndex: "phoneNumber",
    key: "phoneNumber",
    width: "40%"
  }
];
export default {
  components: {
    DetailList,
    DetailListItem
  },
  data() {
    return {
      showBread: false,
      columns,
      id: localStorage.getItem("userId"),
      demandId: this.$route.query.id,
      needList: {},
      matchPeopleDes: {},
      matchOrderDes: "",
      orderDes: "",
      img:require('../../assets/dddb-webEnt/outbg.jpg')
    };
  },
  created() {
    this.$parent.showMenu = false;
    this.$parent.showSiderMenu = true;
  },
  mounted() {
    this.getDemandDetail();
    this.getOrderDetail();
  },
  computed: {
    list() {},
    paginationPropsDemand() {
      let pag = {
        pageSize: 3,
        total: this.peopleData.length
      };
      return pag;
    },
    paginationPropsOrder() {
      let pag = {
        pageSize: 6,
        total: this.orderData.length
      };
      return pag;
    },
    peopleData() {
      let list = [...this.matchPeopleDes];
      return list;
    },
    orderData() {
      let list = [...this.matchOrderDes];
      return list;
    }
  },
  methods: {
    routerTo(isPath,linkOrShow){
      if (isPath === true) {
        this.$router.push(linkOrShow)
      }else {
        // this.showBread = linkOrShow
        window.location.reload()
      }
    },
    // 动态样式
    machineStatusStyle(e){
      console.log('值',e)
      if(e == '匹配中'){
        return {
          'color':'#D39846'
        }
      }else if(e == '匹配失败'){
        return {
          'color':'#ABABAA'
        }
      }else if(e == '匹配完成待支付'){
        return {
          'color':'#314659'
        }
      }else if(e == '交易成功'){
        return {
          'color':'#4F5F46'
        }
      };
    },
    changeBread(key) {
      if (key === "1") {
        this.showBread = false;
      } else {
        this.showBread = true;
      }
    },
    getDemandDetail() {
      getDemandDetailById(this.demandId)
        .then(response => {
          this.needList = response.data.need;
          this.matchPeopleDes = response.data.matchPeople;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getOrderDetail() {
      getOrderDetailById(this.demandId)
        .then(response => {
          console.log(response.data);
          this.orderDes = response.data;
          this.matchOrderDes = response.data.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>

<style lang="less" scoped>
  :global(.ant-form label){
    font-weight: bold;
    font-size:18px;
  }
  :global(.ant-form-text){
    font-size:18px;
  }
.detail-layout {
  margin-left: 44px;
}
.text {
  color: rgba(0, 0, 0, 0.45);
}

.heading {
  color: rgba(0, 0, 0, 0.85);
  font-size: 20px;
}

.no-data {
  color: rgba(0, 0, 0, 0.25);
  text-align: center;
  line-height: 64px;
  font-size: 16px;

  i {
    font-size: 24px;
    margin-right: 16px;
    position: relative;
    top: 3px;
  }
}
.list-control {
  background: #fff;
  width: 100%;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 16px;
  height: 90px;
  display: flex;
  flex-direction: row;
}
.listpage {
  min-height: 50px;
  background: #fff;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 16px 10px 0px 10px;
  width: 100%;
}
</style>



